
import { defineComponent, ref, Ref } from "vue";
import {
  IonContent,
  IonPage,
  IonButtons,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonFooter,
  IonButton,
} from "@ionic/vue";
import StoryComponent from "@/components/StoryComponent.vue";
import { useRoute } from "vue-router";
import { useStore } from "@/store";
export default defineComponent({
  components: {
    IonContent,
    IonPage,
    IonHeader,
    IonButtons,
    IonToolbar,
    StoryComponent,
    IonBackButton,
    IonTitle,
    IonFooter,
    IonButton,
  },
  name: "MarketPlaceDetails",
  setup() {
    const route = useRoute();
    const storyId = route.params.id;
    const store = useStore();
    const story = store.state.stories.find((story) => story.id == storyId);
    return { story };
  },
});
